


















import Vue from 'vue';
import { HeroService } from '@/services/HeroService';
import { Hero } from '@/Hero';

export default Vue.extend({
  name: 'DashboardComponent',
  data() {
    return {
      heroes: [] as Hero[],
      heroService: HeroService
    };
  },
  created() {
    this.getHeroes();
  },
  methods: {
    getHeroes() {
      this.heroService
        .getHeroes()
        .then((heroes: Hero[]) => (this.heroes = heroes.slice(1, 5)));
    }
  }
});
